import {WORDS} from '../constants/wordlist'
import {VALIDGUESSES} from '../constants/validGuesses'
import queryString from "query-string";

export const isWordInWordList = (word: string) => {
  return WORDS.includes(word) || VALIDGUESSES.includes(word)
}

export const isWinningWord = (word: string) => {
  return solution === word
}

const convertTable: { [name: string]: string } = {
  'Q':'ㅂ',
  'W':'ㅈ',
  'E':'ㄷ',
  'R':'ㄱ',
  'T':'ㅅ',
  'Y':'ㅛ',
  'U':'ㅕ',
  'I':'ㅑ',
  'K':'ㅏ',
  'A':'ㅁ',
  'S':'ㄴ',
  'D':'ㅇ',
  'F':'ㄹ',
  'G':'ㅎ',
  'H':'ㅗ',
  'J':'ㅓ',
  'Z':'ㅋ',
  'X':'ㅌ',
  'C':'ㅊ',
  'V':'ㅍ',
  'B':'ㅠ',
  'N':'ㅜ',
  'M':'ㅡ',
  'L':'ㅣ',
}

export const getWordOfDayOrCustomized = () => {
  // January 1, 2022 Game Epoch
  const epochMs = new Date('January 1, 2022 00:00:00').valueOf()
  const now = Date.now()
  const msInDay = 86400000
  const index = Math.floor((now - epochMs) / msInDay)
  const nextday = (index + 1) * msInDay + epochMs

  const queryStringDict = queryString.parse(window.location.search);
  const encodedGameData: string = queryStringDict['code'] as string;
  let solution = WORDS[index % WORDS.length];
  if (encodedGameData !== undefined) {
    const [alphabetAnswer, author] = atob(encodedGameData).split('_');
    solution = [...alphabetAnswer.split('')]
        .map((alphabet) => convertTable[alphabet])
        .join('');
    console.log(author);
  }

  return {
    solution: solution,
    solutionIndex: index,
    tomorrow: nextday,
  }
}

export const { solution, solutionIndex, tomorrow } = getWordOfDayOrCustomized()
